            <span class="mx-2">{{ parent.creditCardTablePagingInfo }}</span>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="cardNumber">
          {{ this.$t('CARD_NUMBER') }}
        </vs-th>
        <vs-th sort-key="embossedName">
          {{ this.$t('NAME_ON_CARD') }}
        </vs-th>
        <vs-th sort-key="expiryDate">
          {{ this.$t('EXPIRY_DATE') }}
        </vs-th>
        <vs-th sort-key="creditLimit">
          {{ this.$t('CREDIT_LIMIT') }}
        </vs-th>
        <vs-th sort-key="availableCreditAmount">
          {{ this.$t('AVAILABLE_CREDIT') }}
        </vs-th>
        <vs-th sort-key="statusName">
          {{ this.$t('STATUS') }}
        </vs-th>
      </template>

