<script>
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'

export default {
  name: 'credit-card-list-layout',
  mixins: [NumberStyleMixin],
  props: {
    parent: {
      type: Object,
      required: true
    }
  }
}
</script>
