import CardService from '@/services/card/CardService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let CreditCardTableMixin = {
  mixins: [ListLayoutMixin, LoadingMixin],
  data: function () {
    return {
      creditCardTableData: [],
      creditCardTableContainer: 'loading-container',
      creditCardTableBusy: false,
      creditCardTableTotalRows: 0,
      creditCardTableCurrentPage: 1,
      creditCardTablePerPage: 10,
      columnDefs: [{
        headerName: this.$t('CARD_NUMBER'),
        field: 'cardNumber',
        format: 'Number'
      }, {
        headerName: this.$t('NAME_ON_CARD'),
        field: 'embossedName'
      }, {
        headerName: this.$t('EXPIRY_DATE'),
        field: 'expiryDate',
        format: 'Date'
      }, {
        headerName: this.$t('CREDIT_LIMIT'),
        field: 'creditLimit',
        format: 'Decimal'
      }, {
        headerName: this.$t('AVAILABLE_CREDIT'),
        field: 'availableCreditAmount',
        format: 'Decimal'
      }, {
        headerName: this.$t('MINIMUM_AMOUNT_DUE'),
        field: 'minimumDueAmount',
        format: 'Decimal'
      }, {
        headerName: this.$t('PAYMENT_DUE_DATE'),
        field: 'paymentDueDate',
        format: 'Date'
      }, {
        headerName: this.$t('STATUS'),
        field: 'statusName'
      }]
    }
  },
  computed: {
    creditCardTableNoDataText: function () {
      return this.creditCardTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    creditCardTableTotalPages: function () {
      return Math.ceil(this.creditCardTableTotalRows / this.creditCardTablePerPage)
    },
    creditCardTablePagingInfo: function () {
      return this.pagingInfo(this.creditCardTableCurrentPage, this.creditCardTablePerPage, this.creditCardTableTotalRows)
    }
  },
  methods: {
    loadCreditCardTableData (loadingContainerId) {
      let self = this
      this.creditCardTableBusy = true
      this.creditCardTableContainer = loadingContainerId || this.creditCardTableContainer
      this.startLoading(this.creditCardTableContainer)
      CardService.creditCards({}).then(function (response) {
        self.stopLoading(self.creditCardTableContainer)
        self.creditCardTableBusy = false
        self.creditCardTablePerPage = response.data.totalRows
        self.creditCardTableTotalRows = response.data.totalRows
        self.creditCardTableData = response.data.cardList || []
      }).catch(function (error) {
        self.stopLoading(self.creditCardTableContainer)
        self.creditCardTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshCreditCardTable () {
      this.loadCreditCardTableData()
    }
  },
  watch: {
    creditCardTableCurrentPage: function () {
      this.refreshCreditCardTable()
    }
  }
}

export default CreditCardTableMixin
