<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`CreditCard${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import CreditCardListLayout from '@/views/card/credit/credit-card-root/credit-card-browse/CreditCardListLayout'
import CreditCardCardLayout from '@/views/card/credit/credit-card-root/credit-card-browse/CreditCardCardLayout'
import CreditCardPivotLayout from '@/views/card/credit/credit-card-root/credit-card-browse/CreditCardPivotLayout'
import CreditCardTableMixin from '@/mixins/card/CreditCardTableMixin'

export default {
  name: 'credit-card-browse',
  mixins: [CreditCardTableMixin],
  components: {
    CreditCardListLayout,
    CreditCardCardLayout,
    CreditCardPivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    this.loadCreditCardTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
