<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center flex-grow justify-between mb-base">
      <div class="flex flex-wrap-reverse items-center">
        <vs-button v-if="parent.creditCardTableData.length > 0" class="p-3 mr-4" @click="exportXls" type="filled" icon="fa-file-excel" icon-pack="fal">{{ $t('EXPORT_XLS') }}</vs-button>
        <vs-button v-if="parent.creditCardTableData.length > 0" class="p-3 mr-4" @click="exportCsv" type="filled" icon="fa-file-csv" icon-pack="fal">{{ $t('EXPORT_CSV') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="parent.refreshCreditCardTable" type="filled" icon="fa-sync" icon-pack="far">{{ $t('REFRESH') }}</vs-button>
      </div>
    </div>

    <ag-grid-vue class="ag-theme-balham ag-pivot-layout"
                 :gridOptions="gridOptions"
                 :rowData="parent.creditCardTableData"
                 :sideBar="sideBar"
                 :statusBar="statusBar">
    </ag-grid-vue>
  </nv-card>
</template>

<script>
import PivotLayoutMixin from '@/mixins/commons/PivotLayoutMixin'
import { AgGridVue } from 'ag-grid-vue'

export default {
  name: 'credit-card-pivot-layout',
  mixins: [PivotLayoutMixin],
  components: {
    AgGridVue
  },
  beforeMount () {
    this.gridOptions.columnDefs = [{
      headerName: this.$t('CARD_NUMBER'),
      field: 'cardNumber'
    }, {
      headerName: this.$t('NAME_ON_CARD'),
      field: 'embossedName'
    }, {
      headerName: this.$t('EXPIRY_DATE'),
      field: 'expiryDate',
      filter: 'agDateColumnFilter',
      enableRowGroup: true,
      valueFormatter: this.formatDate
    }, {
      headerName: this.$t('CREDIT_LIMIT'),
      field: 'creditLimit',
      type: 'numericColumn',
      filter: 'agNumberColumnFilter',
      enableRowGroup: true,
      valueFormatter: this.formatNumber,
      enableValue: true
    }, {
      headerName: this.$t('AVAILABLE_CREDIT'),
      field: 'availableCreditAmount',
      type: 'numericColumn',
      filter: 'agNumberColumnFilter',
      valueFormatter: this.formatNumber,
      enableValue: true
    }, {
      headerName: this.$t('STATUS'),
      field: 'statusName',
      enableRowGroup: true
    }]
  }
}
</script>
